<template>
  <div class="mymoney">
    <van-nav-bar
      title="我的钱包"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="mainshow">

      <img src="@/assets/金钱袋.png" alt="">
      <p>账户总金额（元）</p>
      <p><span>0.00</span></p>

      <div class="haveM">
        <span>可用余额</span>
        <span>0.00<span>元</span></span>
      </div>

      <div class="nhaveM">
        <span>冻结余额</span>
        <span>0.00<span>元</span></span>
      </div>


      <div class="mignx">
        <a href="#/transactionDetails"><span>交易明细</span></a>
        <hr>
        <a href="#/rechargeDetails"><span>充值明细</span></a>
        <hr>
        <a href="#/requestRefund"><span>申请退款</span></a>
      </div>
    </div>

    <div id="but">
      <van-button type="primary" color="#0f1f41">余额充值</van-button>
    </div>


  </div>
</template>

<script>
export default {
  methods: {
    onClickLeft() {
      window.history.go(-1)
      }
    },
}
</script>

<style lang="less" scoped>

.mymoney{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: lightgray;
}


.van-nav-bar{
    background-color: #0e1e40;
  }
/deep/.van-nav-bar__title{
  color: white;
}
.mainshow{
  // background-color: #eeeeee;
  display: flex;
  align-items: center;
  flex-direction: column;
  img{
    width: 20%;
    margin-top: 20%;
  }
  p{
    font-size:14px;
    span{
      font-size: 30px;
      font-weight: 700;
    }
  }
  .haveM{
    margin-top: 25px;
    width: 35%;
    opacity: 0.5;
    span{
      &:nth-child(2){
        float: right;
      }
    }
  }
  .nhaveM{
    width: 35%;
    margin-top: 10px;
    opacity: 0.5;
    span{
      &:nth-child(2){
        float: right;
      }
    }
  }
}







.mainshow p:nth-of-type(1){
  margin-top: 25px;
  margin-bottom: 20px;
  //background-color: firebrick;
  font-size: 16px;
  font-weight: bolder;
  color:#221111;
}




.mignx{
  //background-color: #157b68;
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 110px;

  //background-color: #157b68;


  a{
    display: flex;justify-content: center;align-items: center;
    width: 100%;
    height: 80%;
    border-right: solid darkgray 2px;
    //color: dodgerblue;
    font-size: 15px;
    //background-color: #0e1e40;
  }
  a:active{
    background-color: darkgray;
  }



}






.van-button{
  position: fixed;

  width: 300px;
  height: 40px;

  bottom: 50px;
  border-radius: 10px;
  left:50%;
  transform: translate(-50%);

  background-color: cornflowerblue;

}




</style>



