<template>
  <div id="Div">

    <van-nav-bar
      title="充值明细"
      left-arrow
      @click-left="onClickLeft"
    />


    <van-empty description="No data" />

  </div>
</template>

<script>
export default {
  name: "RechargeDetails",
  data(){
    return{
    }
  },
  methods:{
    onClickLeft(){
      window.history.back()
    }

  }
}
</script>

<style scoped>
#Div {
  position: absolute;
  width: 100%;
  height: 100%;
}



/*导航栏*/
.van-nav-bar {
  background-color: cornflowerblue;
}
#Div /deep/ .van-nav-bar__title {
  color: white;
  font-size: 20px;
  margin-left: 60px;
}
#Div /deep/ .van-icon {
  color: white;
}

#Div /deep/ .van-empty__description{
  font-size: 18px;
  font-weight: bolder;
  color: whitesmoke;
}


</style>



