<template>
  <div id="Div">

    <van-nav-bar id="top-left-icon"
      title="设置"
      left-arrow
      @click-left="onClickLeft"
    />

    <van-cell title="修改密码" is-link />
    <van-cell title="隐私策略" is-link />
    <van-cell title="用户协议" is-link />
    <a href="tel:18355918156">
      <van-cell title="联系客服" is-link value="201911050X" />
    </a>
    <van-cell title="关于" is-link value="版本号V0.0.1" />


    <div id="but-div">
      <van-button type="info" @click="Exit">退出登录</van-button>
    </div>





  </div>
</template>

<script>





export default {
  name: "Setting",
  data(){
    return{
    }
  },
  methods:{
    onClickLeft(){
      window.history.go(-1)
    },
    Exit(){
      this.$dialog.confirm({
        title: '提示',
        message: '确定退出登录吗？',
      }).then(() => {
          const that = this //用于解决异步跳转
          // on confirm
          this.$toast.success('退出成功');
          setTimeout(function(){
            that.$router.push('/')
          }, 1000);
        })
        .catch(() => {
          // on cancel
        });
    },

  }
}
</script>

<style scoped>
#Div {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: gainsboro;
}

/*导航栏*/
.van-nav-bar {
  background-color: cornflowerblue;
}
#Div /deep/ .van-nav-bar__title {
  color: white;
  font-size: 20px;
  margin-left: 60px;
}
#Div /deep/ .van-nav-bar .van-icon{
  color: white;
}





#but-div button{
  position: fixed;

  width: 300px;
  height: 40px;

  bottom: 15px;
  border-radius: 10px;
  left:50%;
  transform: translate(-50%);

  background-color: cornflowerblue;


}







</style>



