<template>
  <div class="addCar-contart">
    <van-nav-bar
      title="添加爱车"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="mainshow">
      <form>
         <ul>
        <li>
          <span>车辆品牌</span>
          <input type="text" v-model="Carp" class="numCar" placeholder="请输入品牌">
        </li>
        <hr>
        <li>
          <span>车牌</span>
          <input type="text" v-model="numCar" class="numCar" placeholder="请输入车牌"/>
        </li>
        <hr>
        <li>
          <span>车辆类型</span>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <hr>
        <li>
          <span>公里数</span>
          <input type="text" class="numload" v-model.number="numload" placeholder="请输入"/>
        </li>
        <hr>
        <li>
          <span>购车日期</span>
            <el-date-picker
              v-model="value1"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
        </li>
        <hr>
        <li>
          <span>是否为运营车辆</span>
          <van-switch v-model="checked" />
        </li>
        <hr>
        <li>
          <span>默认车辆</span>
          <van-switch v-model="checked1" />
        </li>
          </ul>
        <el-button type="info" @click="reast" class="btn1" round>删除</el-button>
        <el-button type="primary" class="btn2" round>保存</el-button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
   data() {
      return {
        options: [{
          value: '选项1',
          label: '乘用车'
        }, {
          value: '选项2',
          label: '家用车'
        }],
        value: '',
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
        value1:'',
        checked: false,
        checked1:false,
        Carp:'',
        numCar:'',
        numload:''
      }
    },
  methods: {
    onClickLeft() {
      window.history.go(-1)
    },
    reast(){
      this.Carp = ''
      this.numCar = ''
      this.numload = ''
      this.checked = false
      this.checked1 = false
      this.value = ''
      this.value1 = ''
    }
  },
}
</script>

<style lang="less" scoped>
addCar-contart{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #eeeeee;
}
.van-nav-bar{
  background-color: #0e1e40;
  }
/deep/.van-nav-bar__title{
  color: white;
}
.mainshow{
  background-color: #eeeeee;
  position: absolute;
  width: 100%;
  height: 100%;

  hr{
    width: 95%;
    opacity: 0.5;
    margin: 0 10px;
  }
  li{
    height: 40px;
    line-height: 40px;
    padding: 0 10%;
    font-size:13px;
    .numCar{
      font-size:13px;
      float: right;
      margin-top: 10px;
      height: 20px;
      width: 80px;
      border: none;
      background-color: #eeeeee;
      opacity: 0.5;
    }
    .numload{
      font-size:13px;
      float: right;
      margin-top: 10px;
      height: 20px;
      width: 40px;
      border: none;
      background-color: #eeeeee;
      opacity: 0.5;
    }
    // 车辆类型
    .el-select{
      float: right;
      width: 35%;
    }
    /deep/.el-input__inner{
      border: none;
      background-color: #eeeeee;
      font-size: 13px;
      height: 30px;
      padding-right: 20px;
    }
    // 日期
    .el-input{
      float: right;
      width: 40%;
    }
    // 按钮
    .van-switch{
      float: right;
      font-size: 20px;
      margin-top: 8px;
    }
  }
  .el-button{
    margin-top: 50%;
    margin-bottom: 10%;
    padding: 12px 35px;
  }
  .btn1{
    margin-left: 10%;
  }
  .btn2{
    float: right;
    margin-right: 10%;
  }
}
</style>



