<template>
  <div id="Div">

    <van-nav-bar
      title="体验金"
      left-arrow
      @click-left="onClickLeft"
    />

  </div>
</template>

<script>
export default {
  name: "ExperienceGold",
  data(){
    return{
    }
  },
  methods:{
    onClickLeft(){
      this.$router.push('/')
    }

  }
}
</script>

<style scoped>
#Div {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../res/Nodate.png") center center;
}

/*导航栏*/
.van-nav-bar {
  background-color: cornflowerblue;
}
#Div /deep/ .van-nav-bar__title {
  color: white;
  font-size: 20px;
  margin-left: 60px;
}
#Div /deep/ .van-icon {
  color: white;
}





</style>



