<template>
  <div id="Div">

    <van-nav-bar title="个人中心">
      <template #right>
        <div id="top_right" @click="ToSetting">
          <img src="../res/setting.png" alt="">
        </div>
      </template>
    </van-nav-bar>

    <div id="info">
      <img src="../res/User.jpg" alt="" style="width: inherit;height: auto">
      <p>18355918156</p>
    </div>

    <!--卡片风格 -->
    <van-cell-group inset id="card1">
      <ul id="ul_1" style="display: flex;justify-content: center;align-items: center">
        <li @click="ToAmount">
          <span>0.00</span>
          <span>余额(元)</span>
        </li>
        <li @click="ToRechargeDetails">
          <span>0.00</span>
          <span>体验金(元)</span></li>
        <li @click="ToCollection">
          <span>0</span>
          <span>收藏(个)</span>
        </li>
      </ul>
    </van-cell-group>

    <van-cell-group inset id="card2">
      <div>
        <van-cell title="我的爱车">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon>
            <div id="righ_small_div" @click="AddCar">
              <p>添加爱车</p>
              <van-icon name="plus" class="icon_add"/>
            </div>
          </template>
        </van-cell>

        <ul>
          <li @click="AddCar">爱车1</li>
          <li>爱车2</li>
          <li>爱车3</li>
        </ul>


      </div>
    </van-cell-group>

    <van-cell-group inset>
      <!--弹出框-->
      <van-cell title="车辆检测报告" icon="location-o" is-link @click="ToReport"/>
      <!--页面跳转-->
      <van-cell title="我的钱包" icon="location-o" is-link to="/MyWallet" />
      <van-cell title="交易记录" icon="location-o" is-link to="/TransactionDetails" />
      <van-cell title="充电订单" icon="location-o" is-link to="/ChargingOrders" />
    </van-cell-group>

    <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o" name="home" to="/home">首页</van-tabbar-item>
      <van-tabbar-item icon="location-o" to="/map">地图</van-tabbar-item>
      <van-tabbar-item icon="balance-list-o" to="/chargingOrders">订单</van-tabbar-item>
      <van-tabbar-item icon="friends-o" to="/user">用户</van-tabbar-item>
    </van-tabbar>

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      active:3,
    }
  },
  methods:{
    ToSetting(){
      this.$router.push('/Setting')
    },

    ToAmount(){
      this.$router.push('/MyWallet')
    },
    ToRechargeDetails(){
      this.$router.push('/ExperienceGold')
    },
    ToCollection(){
      this.$router.push('/Collection')
    },
    AddCar(){
      this.$router.push('/addCar')
    },


    // 提示弹出框
    ToReport(){
      this.$dialog.alert({
        // title:'标题呀',
        message:'暂无检测报告'
      }).then(()=>{
      })
    },








  }
}
</script>


<style scoped>

/*导航栏*/
.van-nav-bar {
  background-color: cornflowerblue;
}
#Div /deep/ .van-nav-bar__title {
  color: white;
  font-size: 20px;
  margin-left: 10px;
}

#Div #top_right{
  position: fixed;
  right: 0;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  display: flex;justify-content: center;align-items: center;
}
#Div #top_right:active{
  background-color: darkgray;
}
#Div #top_right img{
  width: 25px;
  height: 25px;
}






#info{
  width: 70px;
  height: 70px;
  margin: 20px auto 40px auto;
}
#info img{
  border-radius: 70px;
}
#info p{
  color: white;
  margin-left: -9px;
  margin-top: 3px;
  font-size: 14px;
}




#card1{
  margin-top: 15px;
  margin-bottom: 15px;
}
#ul_1{
  height: 65px;
}
#ul_1 li{
  width: 100%;
  height: 100%;
  border-right: solid  white;
}
#ul_1 li span{
  display: block;
}
#ul_1 li span:nth-of-type(1){
  display: flex;align-items: center;justify-content: center;
  margin: 10px 0;
  /*background-color: palevioletred;*/
  font-size: 18px;
  color: cornflowerblue;
}
#ul_1 li span:nth-of-type(2){
  display: flex;justify-content: center;align-items: center;
  font-size: 13px;
  font-weight: bolder;
  color: darkgrey;
}
#ul_1 li:active{
  background-color: #ebedf0;
}







#card2{
  height: 125px;
  margin-bottom: 15px;
}
#card2 p{
  color: grey;
  font-size: 11px;
  font-weight: bold;
}

#card2 .icon_add{
  margin-top: 6px;
  color: grey;
  font-size: 11px;
  font-weight: bold;
}

#card2 #righ_small_div{
  display: flex;
}
#card2 #righ_small_div:active{
  background-color: #ebedf0;
}


#card2 ul{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 70px;

}
#card2 ul li{
  display: flex;justify-content: center;align-items: center;

  width: 65px;
  height: 65px;
  border: solid 1px darkgray;
  border-radius: 10px;
}

#card2 ul li:active{
  background-color: #ebedf0;
}




</style>



