<template>
  <div class="Slocation-contart">
    <van-nav-bar
      title="站点列表"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
  <div class="mainshow">
    <!-- 搜索框 -->
    <van-search @focus="seacher" shape="round" background="#eeeeee" placeholder="目的地/电站名"/>
    <!-- 站点地 -->
    <LocationK></LocationK>
    <LocationK></LocationK>
  </div>
</div>
</template>

<script>
import LocationK from '@/components/LocationInfo/lacationK.vue'
export default {
  methods: {
    onClickLeft() {
      window.history.go(-1)
    },
    seacher(){
      this.$router.push('/search')
    }
  },
  components:{
    LocationK,
  }
}
</script>

<style lang="less" scoped>
.van-nav-bar{
    background-color: #0e1e40;
  }
/deep/.van-nav-bar__title{
  color: white;
}
</style>



