<template>
  <div class="seacher-contart">

    <van-icon @click="back" size="20px" class="back" name="arrow-left" />
    <van-search shape="round" background="#eeeeee" placeholder="目的地/电站名"/>

    <div class="showmain">
       <div class="Seacherhistory">
         <div class="title">搜索历史</div>
         <div class="clear">清空<van-icon name="delete" /></div>
       </div>
     </div>

  </div>
</template>

<script>
export default {
  methods:{
    back(){
      window.history.go(-1)
    }
  }
}
</script>

<style lang="less" scoped>
.seacher-contart{

  position: absolute;
  width: 100%;
  height: 100%;
  background-color: whitesmoke;

  .van-search{
    padding-left: 30px;
  }
  .back{
    position: absolute;
    top: 17px;
    left: 5px;
  }

  .showmain{
    .Seacherhistory{
      background-color: white;
      height: 120px;
      display: flex;
      justify-content: space-between;
      .title{
        font-size: 16px;
        font-weight: 500;
        padding: 10px 8px;
      }
      .clear{
        font-size: 16px;
        padding: 10px 8px;
      }
    }
  }


}
</style>



