<template>
  <div class="mycollter-contart">
    <van-nav-bar
      title="我的收藏"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"/>
    <div class="mainshow">
      <Slocation></Slocation>
      <Slocation></Slocation>
    </div>
  </div>
</template>

<script>
import Slocation from '@/components/LocationInfo/lacationK'
export default {
  methods: {
    onClickLeft() {
      window.history.go(-1)
      }
    },
  components:{
    Slocation
  }
}
</script>

<style lang="less" scoped>
.van-nav-bar{
    background-color: #0e1e40;
  }
/deep/.van-nav-bar__title{
  color: white;
}
</style>


