import Vue from 'vue'
import Router from 'vue-router'




import ExperienceGold from "@/components/ExperienceGold";

import Setting from "@/components/Setting";




import Home from '@/view/Home'
import Location from '@/view/Lacaltion'
import ChargingOrders from '@/view/ChargingOrders'
import User from '@/view/User'

import Search from "@/view/page/Search";

import SearchLocation from "@/view/page/SearchLocation";

import MyLoveCar from "@/view/page/MyLoveCar";
import AddCar from "../components/Car/AddCar";

import Mycollter from "@/view/page/mycollter"

import MyWallet from "@/components/MyWallet";

import TransactionDetails from "@/components/WalletContent/TransactionDetails"
import RechargeDetails from "@/components/WalletContent/RechargeDetails"
import RequestRefund from "@/components/WalletContent/RequestRefund"

import QrCode from "@/view/page/QrCode";






Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',redirect:'/home'},

    {path:'/home',component:Home},
    {path:'/location', component: Location},
    {path:'/chargingOrders', component: ChargingOrders},
    {path:'/user', component: User},

    {path:'/search',component:Search},
    {path:'/searchlocation',component:SearchLocation},

    {path:'/mylovecar',component:MyLoveCar},
    {path:'/addCar',component:AddCar},

    {path:'/collection',component: Mycollter},

    {path:'/mywallet',component: MyWallet},

    {path:'/transactionDetails',  component: TransactionDetails},
    {path:'/rechargeDetails',  component: RechargeDetails},
    {path:'/requestRefund',  component: RequestRefund},

    {path:'/qrcode',  component: QrCode},
    // {path:'/amap',  component: AMap},




    {path:'/ExperienceGold', name:"ExperienceGold", component: ExperienceGold},
    {path:'/Setting', name:"Setting", component: Setting},






  ]
})



