<template>
  <div class="LoveCar-contart">
    <van-nav-bar
      title="我的爱车"
      left-text="返回"
      right-text="添加"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <div class="mainshow">
      <Lcar></Lcar>
      <Lcar></Lcar>
    </div>
  </div>
</template>

<script>
import Lcar from '@/components/Car/MyLoveCarList.vue'
export default {
  methods: {
    onClickLeft() {
      window.history.go(-1)
    },
    onClickRight() {
      this.$router.push('/addCar')
    }
  },
  components:{
    Lcar
  }
}
</script>
<style lang="less" scoped>
.van-nav-bar{
    background-color: #0e1e40;
  }
/deep/.van-nav-bar__title{
  color: white;
}
</style>



