<template>
  <div id="Div">

    <van-nav-bar
      title="二维码扫描"
      left-text="返回"
      right-text="相册"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />

    <qrcode></qrcode>

  </div>

</template>

<script>
import qrcode from '@/components/qrcode.vue';
export default {
  name: "QrCode.vue",
  components: {
    qrcode,
  },
  methods:{
    onClickLeft() {
      this.$router.push('/')
    },
  }
}
</script>

<style scoped>

#Div{
  position: absolute;
  width: 100%;
  height: 100%;
}


#Div /deep/ .van-nav-bar__content{
  width: 100%;
  background-color: #0e1e40;
  position: fixed;
}


.van-nav-bar{
  background-color: #0e1e40;
}

#Div /deep/ .van-nav-bar__title{
  color: white;
}

</style>



