<template>
  <div class="home-contart">

    <div class="heard">
      <van-icon name="map-marked" />
      <span>定位的位置</span>
    </div>

    <!-- 搜索框 -->
    <div class="search">
      <van-search @focus="seacher" shape="round" placeholder="请输入搜索关键词"/>
    </div>


    <div id="swipe">
      <!-- 轮播图 -->
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item><img src="https://tse4-mm.cn.bing.net/th/id/OIP-C.uPYw68R0eepXdgg8fSzTQAHaEK?w=303&h=180&c=7&r=0&o=5&dpr=1.25&pid=1.7" alt=""></van-swipe-item>
        <van-swipe-item><img src="https://tse4-mm.cn.bing.net/th/id/OIP-C.uPYw68R0eepXdgg8fSzTQAHaEK?w=303&h=180&c=7&r=0&o=5&dpr=1.25&pid=1.7" alt=""></van-swipe-item>
        <van-swipe-item><img src="https://tse4-mm.cn.bing.net/th/id/OIP-C.uPYw68R0eepXdgg8fSzTQAHaEK?w=303&h=180&c=7&r=0&o=5&dpr=1.25&pid=1.7" alt=""></van-swipe-item>
        <van-swipe-item><img src="https://tse4-mm.cn.bing.net/th/id/OIP-C.uPYw68R0eepXdgg8fSzTQAHaEK?w=303&h=180&c=7&r=0&o=5&dpr=1.25&pid=1.7" alt=""></van-swipe-item>
      </van-swipe>
    </div>

    <!-- 功能模块 -->
    <div class="gongn">

      <ul>
        <li>
          <a href="#/searchlocation">
            <img src="../assets/站点.png" alt="">
            <p>查找站点</p>
          </a>
        </li>
        <li>
          <a href="#/mylovecar">
            <img src="../assets/汽车.png" alt="">
            <p>我的爱车</p>
          </a>
        </li>
        </ul>
        <ul>
          <li>
          <a href="#/collection">
             <img src="../assets/收藏夹.png" alt="">
             <p>我的收藏</p>
          </a>
        </li>
        <li>
          <a href="#/mywallet">
            <img src="../assets/钱包2.png" alt="">
            <p>我的钱包</p>
          </a>
        </li>
        </ul>
    </div>

    <!--二维码 -->
    <div id="qrcode">
      <button @click="toScan">
        <img src="../res/scan.png" alt="">
        <span>扫码充电</span>
      </button>
    </div>

    <!--Bar -->
    <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o" name="home" to="/home">首页</van-tabbar-item>
      <van-tabbar-item icon="location-o" to="/location">地图</van-tabbar-item>
      <van-tabbar-item icon="balance-list-o" to="/chargingOrders">订单</van-tabbar-item>
      <van-tabbar-item icon="friends-o" to="/user">用户</van-tabbar-item>
    </van-tabbar>

  </div>
</template>

<script>

export default {
  data() {
    return {
      active: 0,
    }
  },
  methods:{
    seacher(){
      this.$router.push('/Search')
    },
    toScan(){
      this.$router.push('/qrcode')
    }
  },
}
</script>

<style lang="less" scoped>


.home-contart{
  position: absolute;
  width: 100%;
  height: 100%;

  /deep/ .heard .van-icon{
    color: black;
  }


  .search{
    width: 100%;
  }

  /deep/ .search .van-search{
    width: 100%;
    height: 25px;
    background-color: #dfdede;
    padding-left: 10px;
    border-radius: 20px;

    .van-search__content{
      height: 25px;
      display: flex;justify-content: center;align-items: center;
    }
  }

  .heard{
    margin: 10px 10px 10px 10px;
    span{
      color: black;
      margin: 0 0 0 5px;
    } }

   // 轮播图
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    text-align: center;
  }
  .my-swipe{
    margin-top: 10px;
    height: 200px;
    img{
     width: 100%;
     height: 200px;
    }
  }

  // 功能模块
  .gongn{
    background-color: whitesmoke;

    border-radius: 20px;
    margin: 15px 10px 15px 10px;
    padding: 10px 0 10px 0;


    ul{
      display: flex;
      justify-content: space-between;
      //background-color: #157b68;
      padding: 10px 0;

      li{
        width: 45%;
        img{
          padding: 0 30% 0 30%;
          width: 40%;
        }
        p{
          font-weight: bolder;
          font-size: 12px;
          text-align: center;
          margin: 0;
        }
      }
    }
  }

  // 扫码
  #qrcode{
    width: 100px;
    height: 100px;
    background-color: white;

    margin-top: -3%;
    border-radius: 90px;
    margin-left: 50%;
    transform: translate(-50%);
    display: flex;justify-content: center;align-items: center;

    button{
      border: solid darkgray 2px;
      width: 95%;
      height: 95%;
      border-radius: 90px;
      img{
        width: 60%;
      }
      span{display: block;font-size: 14px;color: midnightblue;}
    }

    button:active{
      background-color:whitesmoke;
    }
  }

}
</style>



