<template>
<div id="Div">

  <van-nav-bar
    title="充电订单"
    left-arrow
    @click-left="onClickLeft"
  />

  <van-tabs v-model="active" type="card"  swipeable>
    <van-tab title="全部订单" class="page1">
      <div>
        <van-button type="info">点击刷新</van-button>
      </div>
    </van-tab>

    <van-tab title="进行中" class="page1">
      <div>
        <van-button type="info">点击刷新</van-button>
      </div>
    </van-tab>

    <van-tab title="待支付" class="page1">
      <div>
        <van-button type="info">点击刷新</van-button>
      </div>
    </van-tab>

    <van-tab title="已完成" class="page1">
      <div>
        <van-button type="info">点击刷新</van-button>
      </div>
    </van-tab>
  </van-tabs>

  <van-tabbar v-model="activBbar" id="bar">
    <van-tabbar-item icon="home-o" name="home" to="/home">首页</van-tabbar-item>
    <van-tabbar-item icon="location-o" name="map" to="/location">地图</van-tabbar-item>
    <van-tabbar-item icon="balance-list-o"  name="order" to="/chargingOrders">订单</van-tabbar-item>
    <van-tabbar-item icon="friends-o" name="user" to="/user">用户</van-tabbar-item>
  </van-tabbar>

</div>
</template>

<script>
export default {
  name: "ChargingOrders",
  data(){
    return{
      activBbar: 2,
    }
  },
  methods:{
    onClickLeft(){
      window.history.go(-1)
    }

  }
}
</script>



<style lass="scss" scoped>

#Div {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../res/Nodate.png") center center;
}

/*导航栏*/
.van-nav-bar {
  background-color: cornflowerblue;
}
#Div /deep/ .van-nav-bar__title {
  color: white;
  font-size: 20px;
  margin-left: 60px;
}
#Div /deep/ .van-nav-bar .van-icon{
  color: white;
}


/*标签页*/
#Div /deep/ .van-tabs__wrap{
  height: 35px;
  margin-top: 10px;
  margin-bottom: 10px;
}
#Div /deep/ .van-tabs__nav--card {
  height: 35px;
  border-radius: 20px;
  border: white;
}
#Div /deep/ .van-tab{
  border-radius: 20px;
}
#Div /deep/ .van-tabs__nav--card .van-tab {
  color: black;
  border-right:  white;
}
#Div /deep/ .van-tabs__nav--card .van-tab.van-tab--active{
  color: #fff;
  background-color: cornflowerblue;
}

/*标签页中内容*/
.page1 div button{
  margin-top: 400px;
  height: 35px;
  border-radius: 20px;
  /*设置垂直水平居中*/
  left:50%;
  top:50%;
  transform: translate(-50%, -50%);
}

</style>



